* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  --webkit-font-smoothing: antialiased;
  --moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
}

main {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 10px;
}

.setting_logo {
  width: 30px;
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}

.ss_container {
  box-sizing: border-box;
  height: 70vh;
  overflow-y: auto;
  border: 1px solid green;
  border-radius: 7px;
}
.ss_container .station_sort {
  position: sticky;
  top: 0;
  background-color: #fff;
  padding-bottom: 5px;
  border-bottom: 1px solid green;
  border-radius: 7px;
}
.ss_container .radioStation_btn_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  padding-top: 5px;
}
.ss_container .radioStation_btn_container div {
  margin-bottom: 5px;
}
.ss_container .radioStation_btn_container button {
  cursor: pointer;
  margin-bottom: 5px;
  width: 100px;
  height: 100px;
  padding: 5px;
}
.ss_container .radioStation_btn_container button img {
  width: 100%;
  height: auto;
}

.favorite__btn {
  padding: 5px;
  margin: 5px;
  border-radius: 7px;
  border: 1px solid green;
  width: 40%;
}
.favorite__btn_addDel {
  width: 60%;
}

.player {
  height: 30%;
}
.player .playStop_btn {
  border-radius: 50%;
  height: 70px;
  width: 70px;
  align-content: center;
  text-align: center;
  border: none;
  margin: 10px 0;
}